.product{
    box-sizing: border-box;
    width: 300px;
    height: 370px;
    display: grid;
    grid-template-rows: 1 3fr;
    text-align: center;
    justify-content: center;
    border-radius: 25px;
    border: 2px solid rgb(115, 113, 113);
    padding: 15px;
    box-shadow: 5px 5px 5px grey;
    align-items: center;
    transition: 0.3s ease;
}
.product:hover{
    background-color: rgb(169, 169, 169);
    scale: 1.1;
}
.product img{
    width: 100%;
   height: 220px;
   border-radius: 15px;
   transition: 0.5s ease;
}
.product img:hover{
    scale: 1.1;
}
.product button{
    border: none;
    outline: none;
    background-color: rgb(0, 102, 255);
    color: #fff;
   padding: 5px;
   border-radius: 50px;
   width: 100px;
   transition: 0.3s ease;
}
.product button:hover{
    background-color: black;
}
.product button:active{
    scale: 1.1;
}